import axios from 'axios'
import { useEffect, useState, forwardRef } from 'react'
import Select from '@mui/material/Select'
import MenuItem from '@mui/material/MenuItem'
import Grid from '@mui/material/Grid'
import Card from '@mui/material/Card'
import Typography from '@mui/material/Typography'
import Input from '@mui/material/Input';
import Button from '@mui/material/Button'
import { InputLabel } from '@mui/material'
import { FormControl } from '@mui/material'
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import LinearProgress from '@mui/material/LinearProgress';
import Box from '@mui/material/Box';
import SelectJson from './select.json'
import Lottie from 'react-lottie-player'

const Alert = forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function App() {

  const [open, setOpen] = useState(false)
  const [isLoading, setIsLoading] = useState(false)

  const [classifier, setClassifier] = useState('')
  const [sdrr, setSdrr] = useState('')
  const [medianRR, setMedianRR] = useState('')
  const [hr, setHr] = useState('')
  const [lf, setLf] = useState('')
  const [rmssd, setRmssd] = useState('')
  const [pnn50, setPnn50] = useState('')
  const [hf, setHf] = useState('')
  const [hflf, setHflf] = useState('')
  const [meanRR, setMeanRR] = useState('')

  const [result, setResult] = useState('')

  //image
  const [fileInputState, setFileInputState] = useState("");
  const [previewSource, setPreviewSource] = useState("");
  // eslint-disable-next-line
  const [selectedFile, setSelectedFile] = useState();

  const setDefault = () =>{
      setSdrr('881.757865')
      setMedianRR('893.460030')
      setHr('90.370537')
      setLf('15.720468')
      setRmssd('68.809562')
      setPnn50('0.133333')
      setHf('1005.981659')
      setHflf('18.181913')
      setMeanRR('0.018074')
  }


  

  const handleFileInputChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      previewFile(file);
      setSelectedFile(file);
      setResult('')
      setFileInputState(e.target.value);
      setDefault()
    }
  };

  const previewFile = (file) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = () => {
      setPreviewSource(reader.result);
    };
  };

  const handleClose = () =>{
      setOpen(false)
  }

  const handleChange = (event) => {
    setResult('')
    setClassifier(event.target.value);
  };

  const handleSubmit = e => {
    e.preventDefault();
    setIsLoading(true)
    setResult('')
    const payload = {
      classifier, sdrr, medianRR, hr, lf, rmssd, pnn50, hf, hflf, meanRR
    }


    axios.post('/',payload).then(res=>{
        setResult(res.data.result)
        setIsLoading(false)
      }).catch(err=>{
        setIsLoading(false)
        setOpen(true)
      })
  }

  useEffect(()=>{
      
  },[])

  return (
    <div style={{width:"100vw"}}>
     {
         isLoading && <Box style={{position:'absolute', top: 0}} sx={{ width: '100%' }}>
         <LinearProgress />
       </Box>
     } 
     <div style={{backgroundColor:'#fec5bb', paddingBottom:"12px"}}>
     <center><Typography variant="h4">  Diabetes Prediction from ECG <img style={{transform:"translateY(8px)"}} width='50px' height='50px' alt='' src="https://img.icons8.com/external-justicon-flat-justicon/64/000000/external-heart-rate-hospital-and-medical-justicon-flat-justicon.png"/> </Typography></center>
     </div>

     
     <Grid container style={{marginTop:"46px"}} >
         <Grid item xs={12} sm={6}>
        <div style={{padding:"12px"}}>
         <input
              id="contained-button-file"
              type="file"
              accept="image/*"
              name="image"
              onChange={handleFileInputChange}
              value={fileInputState}
              className="form-input"
              style={{ display: "none" }}
            />

            <label htmlFor="contained-button-file">
            {previewSource ? 
              <div
                component="span"
                style={{display:"flex",flexDirection:"column", justifyContent:"center",backgroundColor:'#fff', alignItems:"center", width:'100%',height:"414px", borderWidth:'2px', borderColor:'rgba(63,81,181,0.8)', borderRadius:'4px', borderStyle:'dashed'}}
              >
             
                    <img
                      src={previewSource}
                      alt=""
                      style={{
                        width: "100%",
                        borderRadius: "4px",
                        overflow: "hidden",
                        
                      }}
                    />
               
              </div>
             :

                <div component="span" style={{display:"flex",flexDirection:"column", justifyContent:"center",backgroundColor:'#fff', alignItems:"center", width:'100%',height:"414px", borderWidth:'2px', borderColor:'rgba(63,81,181,0.8)', borderRadius:'4px', borderStyle:'dashed'}}>
                <div><Lottie
                        loop={false}
                        animationData={SelectJson}
                        play
                        style={{ width: 300, height: 300 }}
                    />
                    </div>
                    <div style={{color:'rgb(0,137,255)'}}>
                    <Typography variant="h6" style={{transform:"translateY(-80px)"}}>Select ECG image</Typography>
                    </div>
                </div>
            }
            </label>



           
            </div>
         </Grid>

         <Grid item xs={12} sm={6}>
         <center>
    <div>
      <Card style={{padding:"12px", margin:"12px"}}>
      <form autoComplete="off" onSubmit={handleSubmit} onChange={()=>setResult('')}>
       
                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                    CLASSSIFIER
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                <FormControl fullWidth> <InputLabel id="demo-simple-select-label">Select classifier</InputLabel>
                                    <Select
                                        required
                                        labelId="demo-simple-select-label"
                                        id="demo-simple-select"
                                        value={classifier}
                                        label="Select classifier"
                                        onChange={handleChange}
                                      >
                                        <MenuItem value={'Decision Tree'}>Decision Tree</MenuItem>
                                        <MenuItem value={'Weighted k-NNC'}>Weighted k-NNC</MenuItem>
                                        <MenuItem value={'Neural Network'}>Neural Network</MenuItem>
                                      </Select>
                                      </FormControl>
                                   
                                </div>
                                </Grid>

                            </Grid>
                            
                   


                        <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                    SDRR
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={sdrr} onChange={(e)=>setSdrr(e.target.value)} placeholder="Enter SDRR" />
                                   
                                </div>
                                </Grid>

                            </Grid>

                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                    MEDIAN RR
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={medianRR} onChange={(e)=>setMedianRR(e.target.value)} placeholder="Enter MEDIAN RR" />
                                   
                                </div>
                                </Grid>

                            </Grid>


                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                   HR
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={hr} onChange={(e)=>setHr(e.target.value)} placeholder="Enter HR" />
                                   
                                </div>
                                </Grid>

                            </Grid>
                     

                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                   LF
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={lf} onChange={(e)=>setLf(e.target.value)} placeholder="Enter LF" />
                                   
                                </div>
                                </Grid>

                            </Grid>


                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                    RMSSD
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={rmssd} onChange={(e)=>setRmssd(e.target.value)} placeholder="Enter RMSSD" />
                                   
                                </div>
                                </Grid>

                            </Grid>

                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                    pNN50
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={pnn50} onChange={(e)=>setPnn50(e.target.value)} placeholder="Enter pNN50" />
                                   
                                </div>
                                </Grid>

                            </Grid>


                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                    HF
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={hf} onChange={(e)=>setHf(e.target.value)} placeholder="Enter HF" />
                                   
                                </div>
                                </Grid>

                            </Grid>


                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                    HF-LF
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={hflf} onChange={(e)=>setHflf(e.target.value)} placeholder="Enter HF-LF" />
                                   
                                </div>
                                </Grid>

                            </Grid>


                            <Grid container spacing={0}>
                                <Grid item xs={6}>
                                    <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"row",alignItems:"center"}}>
                                    <Typography style={{marginLeft:"8px"}}>
                                    MEAN RR
                                    </Typography>
                                    </div>
                                    
                                </Grid>
                                <Grid item xs={6}>
                                <div style={{width:"100%",height:"100%",display:"flex",flexDirection:"column",alignItems:"flex-end"}}>
                                    
                                <Input fullWidth type="number" required variant="outlined" value={meanRR} onChange={(e)=>setMeanRR(e.target.value)} placeholder="Enter MEAN RR" />
                                   
                                </div>
                                </Grid>

                            </Grid>
                        

                        <center><Button disabled={!!isLoading} style={{marginTop:"12px"}} type="submit" variant='contained'>Submit</Button></center>

                </form>




      </Card>

      

      
    </div>
    </center>

        </Grid>


    </Grid>
    { result && <Card style={{padding:"12px", margin:"12px",backgroundColor:'#fec5bb'}}>
          <center><Typography variant="h4">
           You are {result.charAt(0).toUpperCase() + result.slice(1)} {result === 'healthy' ? '😊': '😔'}
          </Typography></center>
      </Card>
      }
      
    <Snackbar open={open} autoHideDuration={3000} onClose={handleClose} anchorOrigin={{vertical: 'bottom', horizontal: 'center'}}>
        <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }}>
            Something went wrong
        </Alert>
      </Snackbar>
          

     
    </div>
  )
}

export default App

